.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.hidden {
  display: none; }

.centered {
  display: flex;
  align-items: center;
  justify-content: center; }

.modal-wrapper {
  position: fixed;
  z-index: 900;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: #3d3333;
  opacity: 0.96; }

.content-wrapper {
  margin: 0 auto;
  max-width: 1366px;
  padding-right: 136px;
  padding-left: 136px; }

@media (max-width: 1023px) {
