.booking__time-label {
  position: relative; }

.booking__time-radio + label::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 110px;
  height: 38px;
  border: 2px solid #c4c4c4;
  border-radius: 65px; }

.booking__time-radio:checked + label::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 110px;
  height: 38px;
  background: linear-gradient(180deg, #2cdb52 0%, #13ba19 100%);
  border: none;
  border-radius: 65px; }

.booking__time-radio:disabled + label::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 110px;
  height: 38px;
  background: linear-gradient(158.6deg, #f2901d 9.78%, #d26f13 79.81%);
